<template>
	<view class="back" :style="{'background-image':'url(' + bgImg + ')'}">
		<view class="back_before">
			<view class="key"><{{title}}></view>
			<scroll-view class="scroll" scroll-y="true">
				<view class="poetry">
					<view class="poetry_item" v-for="(item,index) in poetry">{{item}}</view>
				</view>
			</scroll-view>
			
			<view class="btns">
				<view class="leftBtn"></view>
				<view class="playBtn" @click="clickBtn">
					<image src="../static/images/play1.png" v-if="!$store.state.play_status"></image>
					<image src="../static/images/pause1.png" v-else></image>
				</view>
				<view class="rightBtn"></view>
			</view>
			
			<!-- <view style="position: fixed;bottom: 2px;margin-left: 2.5vw;">
				<musicPlayer></musicPlayer>
			</view> -->
		</view>
	</view>
</template>

<script>
	import musicPlayer from '../views/musicPlayer.vue'
	export default{
		components:{
			musicPlayer
		},
		data(){
			return {
				title:"寄人",
				poetry:[
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。",
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。",
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。",
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。",
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。",
					"别梦依依到谢家，小廊回合曲阑斜。",
					"多情只有春庭月，犹为离人照落花。"
				],
				music:"",
				bgImg:"../static/images/styles/bg1.jpg",
			}
		},
		onLoad() {
			let _this = this;
			this.title = this.$store.state.poetry_title;
			this.poetry = this.$store.state.poetry;
			setTimeout(function(){
				_this.$store.state.play_status = true;
				_this.$store.commit("playMusic");
			},1000);
		},
		onBackPress() {
			this.$store.commit("pauseMusic");
			this.$store.commit("changeStatus");
		},
		methods:{
			clickBtn(){
				if(this.$store.state.play_status){
					this.$store.commit("pauseMusic");
				}else{
					this.$store.commit("playMusic");
				}
				this.$store.commit("changeStatus");
				// console.log(this.$store.state.musicPlayer.duration)
			},
		}
	}
</script>

<style>
	.back{
		background-repeat: no-repeat;
		background-size: 100vw 100vh;
		z-index: 1;
	}
	
	.back::before{
		content: "";
		width:100%;
		height:100%;
		position: absolute;
		left:0;
		top:0;
		background: inherit;
		filter: blur(3px);
		z-index: 2;
	}
	
	.back_before{
		z-index: 3;
		position: absolute;
		width: 100vw;
		height: 100vh;
	}
	
	.key{
		height: 5vh;
		text-align: center;
		font-size: 23px;
		margin: 20px 0;
	}
	
	.scroll{
		height: 50vh;
	}
	
	.poetry{
		height: 95vh;
		text-align: center;
		font-family: "楷体","楷体_GB2312";
		font-size: 18px;
		color: #4f4f4f;
	}
	
	.poetry_item{
		margin-top: 10px;
	}
	
	.btns{
		display: flex;
		flex-direction: row;
		align-items: center;
		margin-top: 20vh;
	}
	
	.leftBtn{
		width: 35px;
		height: 35px;
		border-radius: 25px;
		background-color: #f0fffc;
		margin-left: 25vw;
	}
	
	.playBtn{
		width: 50px;
		height: 50px;
		border-radius: 25px;
		background-color: #f0fffc;
		margin: 0 auto;
	}
	
	.playBtn image{
		width: 20px;
		height: 20px;
		margin-left: 15px;
		margin-top: 15px;
	}
	
	.rightBtn{
		width: 35px;
		height: 35px;
		border-radius: 25px;
		background-color: #f0fffc;
		margin-right: 25vw;
	}
</style>
